'use client';
import { useState } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';

export default function TMSSidebar() {
  const [modules, setModules] = useState([
    { name: 'Dashboard', active: false, href: '/tms-module' },
    { name: 'TMS', active: true, href: '/tms-module/dashboard' },
    { name: 'Payroll', active: false, href: '#' },
    { name: 'Procurement', active: false, href: '#' },
    { name: 'Material Management', active: true, href: '#' },
    { name: 'Lookup Manager', active: false, href: '#' },
    { name: 'HR Management', active: true, href: '#' },
    { name: 'Profile', active: false, href: '#' },
    { name: 'Internal Revenue Finance', active: false, href: '#' },
    { name: 'Layouts', active: false, href: '#' },
    { name: 'Pages', active: false, href: '#' }
  ]);

  const [departments, setDepartments] = useState([
    { name: 'Engineering', active: false },
    { name: 'Management', active: true },
    { name: 'Economics', active: true },
    { name: 'Marketing', active: true },
    { name: 'Others', active: true }
  ]);

  const toggleModule = (name: string) => {
    setModules(modules.map(m => 
      m.name === name ? { ...m, active: !m.active } : m
    ));
  };

  const pathname = usePathname();

  return (
    <div className="erp-sidebar">
      <h1 className="erp-title">INSA ERP</h1>
      
      <div className="modules-section">
        <h2>Modules</h2>
        <ul>
          {modules.map(module => (
            <li key={module.name}>
              <Link href={module.href} className={pathname === module.href ? 'active' : ''}>
                <input 
                  type="checkbox" 
                  checked={module.active}
                  onChange={() => toggleModule(module.name)}
                />
                {module.name}
              </Link>
            </li>
          ))}
        </ul>
      </div>

      <div className="departments-section">
        <h2>HR employees based on department</h2>
        <ul>
          {departments.map(dept => (
            <li key={dept.name}>
              <label>
                <input 
                  type="checkbox" 
                  checked={dept.active}
                  onChange={() => setDepartments(departments.map(d => 
                    d.name === dept.name ? { ...d, active: !d.active } : d
                  ))}
                />
                {dept.name}
              </label>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}